
import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import routes from './router/index'
import vuex from 'vuex'
Vue.use(VueRouter)
Vue.use(vuex)
Vue.config.productionTip = false
/* eslint-disable no-new */
const router=new VueRouter({
	routes
})

var store= new vuex.Store({
	state:{
		logined:false
	}
})

new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }

})

router.beforeEach(function(to,from,next){ //进入任何页面之前这是判断未登录先去登录才能付款的界面
 var logined=store.state.logined
	if(!logined && to.path=='/pay'){
		next('/login')
	}else{
		next()
	}
})

router.afterEach(function(to,from){  //进入任何页面之后
	//alert('成功进入该界面')
})



