import login from '../components/login.vue'

export default [
  {
  	path:'/',
  	name:'lookChild',
  	component:{
  		template:`<div><router-link to='/a'>查看子路由(这是在router-view中的内容)</router-link><router-view></router-view></div>`
  	},
  	children:[   //子路由
        {
        	path:'a',
        	component:{
        		template:`<div>这是子路由详情<br><router-link to='/'>返回</router-link></div>`
        	}
        }
          ]
      },
        {             //路由传参
        	path:'/people',
        	name:'people',
        	component:{
        		template:`<div>姓名:{{$route.params.name}} <br><router-link to='/'>返回</router-link></div>`
        	}
        },
        {
        	path:'/login',
        	name:'login',
        	component:login
        },
        {
        	path:'/pay',
        	name:'pay',
        	component:{
        		template:`<div>付款界面<br><router-link to='/'>返回</router-link></div>`
        	}
        }
  	]

